export class BalanceModel {
	id: number;
	categoryId: number;
	categoryName: string;
	amount: number;
	balance: number;
	gasStockTransactionTypeId: number;
	transactionType: string;
	notes: string;
	transactionDate: string;

	clear() {
		this.id = 0;
		this.categoryId = 0;
		this.amount = 0;
		this.balance = 0;
		this.gasStockTransactionTypeId = 0;
		this.categoryName = '';
		this.transactionType = '';
		this.notes = '';
		this.transactionDate = new Date().toISOString().substr(0, 16);
	}
}
